import React from 'react';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';

export const GET_POSTS = gql`
  query GetPosts {
    posts {
      id
      author
      body
    }
  }
`;

function Index() {
    return (
        <Query query={GET_POSTS}>
            {
                ({ loading, error, data }) => {
                    if (loading) {
                        return <p>loading ...</p>
                    }

                    if (error) {
                        return <p>error</p>
                    }

                    return (
                        data.posts.map(post => (
                            <div key={post.id}>
                                <p>{post.author}</p>
                                <p>{post.body}</p>
                            </div>
                        ))
                    )
                }
            }
        </Query>
    )
}

export default Index;